.popup__frame {
    background-color: #ffffff;
    box-sizing: border-box;
    padding: 34px 36px 36.5px;
    border-radius: 10px;
    box-shadow: 0 0 25px 0 rgba(0, 0, 0, 15%);
    color: black;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-items: center;
    position: relative;
    width: 430px;
  }
  
  @media screen and (max-width: 550px) {
    .popup__form {
      width: 282px;
      padding: 25px 22px;
    }
  }
  